.header-component {
	width: 100%;
	text-align: center;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	position: fixed;
	z-index: 2000;
}

.main-content .nav-bar {
}

.search-bar-wrapper {
	position: fixed;
	width: calc(100% - 2em);
	padding: 1em 0;
	z-index: 1000;
	/* width: 100%; */
}

@media screen and (min-width: 723px) {
	.search-bar-wrapper {
		max-width: 723px;
	}
}

@media screen and (min-width: 992px) {
	.search-bar-wrapper {
		max-width: 933px;
	}
}

@media screen and (min-width: 1200px) {
	.search-bar-wrapper {
		max-width: 1127px;
	}
}

.plant-details .img-wrapper {
	text-align: center;
}

.plant-details .img-wrapper p {
	text-align: left;
	margin-bottom: 1em;
}

.add-to-collection-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.loader-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	height: 100%;
}

.plant-list-item .hide {
	display: none;
}

.ui.button.show {
	margin-top: 16px;
}

.plant-results.plant-name {
	transition: all;
	transition-duration: 250ms;
	max-width: 150px;
}

.plant-results.plant-name:hover {
	cursor: pointer;
	opacity: 0.8;
}

@media screen and (min-width: 414px) {
	.plant-results.plant-name {
		max-width: 300px;
	}
}

.plant-results.no-results {
	text-align: center;
	font-style: italic;
}

.plant-results-loader {
	padding: 4em !important;
	margin-top: 1em !important;
}

.plant-results.plant-name {
	text-transform: capitalize;
}

.collection-link {
	margin-top: 1em;
}

* {
	box-sizing: border-box;
}

img {
	max-width: 100% !important;
	margin-bottom: 1em;
}

.lrg-header {
	font-size: 5em !important;
}

.med-font {
	font-size: 1.4em !important;
	padding: 10px 0;
}

.sm-font {
	font-size: 1.2em !important;
	padding: 0 0 5px;
}

/* FONT COLORS  */
.font-main-green {
	color: #698f3f !important;
}

.font-main-blue {
	color: #68c5db !important;
}

.font-main-white {
	color: #fbfcfd !important;
}

.font-off-white {
	color: #edf2f4 !important;
}

.font-disabled {
	color: gray !important;
}

/* BACKGROUND COLORS */
.bg-main-green {
	background-color: #698f3f !important;
}

.bg-main-blue {
	background-color: #68c5db !important;
}

.bg-main-white {
	background-color: #fbfcfd !important;
}

.bg-off-white {
	background-color: #f3f3f3 !important;
}

.main-content {
	padding-top: 99px;
}

a,
a:hover {
	color: inherit;
}

